.read-more-link {
  color: white;
}

.read-more-text {
  display: none;
}

.read-more-text--show {
  display: inline;
}

.read-more-btn {
  color: white;
  text-decoration: underline;
  background-color: transparent;
  border: none;
  font-size: 1rem;

}

.main-page-button {
  background-color: #BF3C1F;
  color: white;
  padding: 1rem;
  border-radius: 12px;
  font-weight: bold;
  margin-top: 2rem;
  margin-bottom: 2rem;
  width: 12rem;

}

.home-page-pics {
  display: grid;
  grid-template-columns: 32% 32% 32%;
  justify-items: center;
  grid-gap: 1.8%;
  margin-top: 2rem;
}

.button-div{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hyperlink{
    color: #BF3C1F;
}
.img-botton-div{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.main-page-text{
  text-align: justify;
}
@media only screen and (max-width: 700px) {

.home-page-container{
    width: 80%;
  }

.home-page-pics{
 
  display: block;

}
}
